
import Component from 'vue-class-component'
import Vue from 'vue'
import { Prop } from 'vue-property-decorator'
import { currency } from '@/modules/core/filters'
import { HistoryOrder } from '@/modules/store/data/resources'

@Component({ name: 'HistoryNormalOrderBreakdownSummary' })
export default class HistoryNormalOrderBreakdownSummary extends Vue {
  @Prop() details!: HistoryOrder

  get totalPriceOfProducts() {
    return currency(this.products?.totalProductsPrice)
  }

  get productDiscounts() {
    return currency(this.products?.partnerProductDiscounts)
  }

  get totalPrice() {
    return currency(this.products?.total)
  }

  get isCashPayment() {
    return this.details && this.details.paymentMethod === 'CASH'
  }

  get hasProductDiscounts() {
    return (
      this.haveProductsBeenRetrieved &&
      this.products?.partnerProductDiscounts &&
      this.products?.partnerProductDiscounts !== 0
    )
  }

  get haveProductsBeenRetrieved() {
    return Boolean(this.products)
  }

  get products() {
    return this.details.products
  }
}
